<template>
  <div>
    <el-row>
      <el-col :span="6">
        <div>
          <el-radio v-model="radio" label="1" @change="edit">
            <img
              width="76%"
              src="static/images/theme1.png"
              alt
            />
            <p>科技感黑色建筑网站主题</p>
          </el-radio>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-radio v-model="radio" label="2" @change="edit">
            <img
              width="76%"
              src="static/images/theme2.png"
              alt
            />
            <p>扁平风格优雅白色建筑网站主题</p>
          </el-radio>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-radio v-model="radio" label="3" @change="edit">
            <img
              width="76%"
              src="http://47.103.26.130:9999/upload/image/2020/02/15/ec6a74c5-01ab-4800-9a13-daaa54b9a2a8.PNG"
              alt
            />
            <p>主题3</p>
          </el-radio>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-radio v-model="radio" label="4" @change="edit">
            <img
              width="76%"
              src="static/images/theme1.png"
              alt
            />
            <p>主题4</p>
          </el-radio>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-radio v-model="radio" label="5" @change="edit">
            <img
              width="76%"
              src="static/images/theme1.png"
              alt
            />
            <p>主题5</p>
          </el-radio>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "pagePermissions",
  data() {
    return {
      radio: "1"
    };
  
  
  }
  
  ,
  created(){
    this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/company/find",
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log("主题",response);
          this.radio=response.data.templates
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
  }
  ,
  
  methods: {
    edit() {
      this.editVisible = false;

      this.$axios({
        method: "put",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/company/updateTemplates",
        data: {templates:this.radio},
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
    }
  }
};
</script>

<style lang="scss" scoped>
img {
  margin: 14px auto;
}
p {
  text-align: center;
}
</style>



